
import XHeader from 'vux/src/components/x-header/index.vue'

import Vue from 'vue'
import navHeader from '../components/nav-header.vue'
import defaultIcon from '../../assets/images/timg3.jpeg'

export default {
components:{
XHeader,
navHeader
},
data(){
return {
list:'title',
listData:[],
defaultIcon
}
},
mounted(){
this.$axios.post('/api/list').then(res=>{
if (res.status==200){
this.listData=res.data.articles;
}
}).catch(res=>{

})
}
}



